<template>
    <div ref="trys" style="height: calc(100% - 0em)" class="all-container">
      <div class="operate-div">
        <template v-for="(item, $index) in FormOpt">
          <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options"
                  @getInput="getFrom"></inputs>
        </template>
      </div>
      <div class="operate-div operateBtn" ref="operateDiv">
        <!--v-if="controls[1]" v-if="controls[3]" -->
        <button v-if="controls[1]"  class="button-operate" @click="operateUser(null)">
          <i class="hasIcon hasImg">
            <img src="dist/img/configuration/add-new-icon.png" />
          </i>
          添加人员
        </button>
        <button v-if="controls[3]" class="button-operate" @click="removeUser">
          <i class="hasIcon hasImg">
            <img src="dist/img/configuration/move-button.png" />
          </i>删除人员
        </button>
      </div>
      <div class="deepFilter" ref="deepFilter" v-if="deepSearchData.length > 0">
        <label>筛选条件：</label>
        <template v-for="(data, $index) in deepSearchData">
            <span class="deepSearchSpan" :style="{'padding-right': data.flag ?  '1.5em' : '0.5em'}" >{{data.label}}： {{data.value}}
              <i v-if="data.flag" @click=" removeSearchData($index)">
                <icon name="remove" scale="1"></icon>
              </i>
            </span>
        </template>
      </div>
      <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 6.5em)'}">
        <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding"
                @rowDbClick="clickTableRow"></tables>
      </div>
      <dialogs type="confirm" ref="conformDialog"></dialogs>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="customer" ref="addUser" v-if="showDialog" width="46em" >
         <div slot="header">
             {{operate === 0 ? '添加人员': '编辑人员'}}
         </div>
        <div slot="content">
          <ul class="dialog-ul">
            <li class="form-list" :class="item.classes" v-for="(item, $index) in dialogFrom" v-show="!item.unShow">
              <label v-if="!item.isEmpty" class="label-show"><span class="emptyTips"> * </span>{{item.label}}</label>
              <label v-if="item.isEmpty" class="label-show"><span > &nbsp; </span>{{item.label}}</label>
              <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" :ref="item.options.key" @getInput = "getFrom"></inputs>
              <selects v-if="item.type === 'select'" :option="item.options" :ref="item.options.key" @getSelect = "getFrom"></selects>
            </li>
          </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="submitData">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
        </div>
      </dialogs>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                showDialog: false, //弹框销毁与重建
                operate: 0, //0:添加1：编辑
                 tableHeight: '',
                searchParam: {
                  userName: '',
                  userMobile: '',
                  roleName: ''
                },
                deepSearchData: [],
                controls: [],
                searchType: 'userName',
                selected: '',
                FormOpt: [
                    {
                        type: 'input',
                        options: {
                            type: 'typeSearch',
                            key: 'searchParam',
                            selectOpt: {
                                width: '7em',
                                cs: 'client',
                                data: [
                                    {text: '人员姓名', value: 'userName'},
                                    {text: '联系电话', value: 'userMobile'},
                                    {text: '人员角色', value: 'roleName'}
                                ],
                                text: 'text',
                                value: 'value',
                                key: 'searchType'
                            }
                        }
                    }
                ],
                TableOpt: {
                    rowName: 'data',
                    column: [
                        {field: 'state',type: 'checkBox', textAlign: 'center',  width: '40'},
                        {title: '姓名', field: 'userName', textAlign: 'center'},
                        {title: '电话', field: 'userMobile', textAlign: 'center'},
                        {title: '登录名', field: 'loginName', textAlign: 'center'},
                        {title: '角色', field: 'roleName', textAlign: 'center'},
                        {title: '部门机构', field: 'orgName', textAlign: 'center'},
                        {title: '邮箱', field: 'userMail', textAlign: 'center'}
                    ],
                    url: 'user/queryList',
                    method: 'post',
//                    url: 'dist/json/user.json',
//                    method: 'get',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20,40,60],
                    height: document.body.clientHeight - 360
                },
                formData : { //人员对象
                  remark: '',
                  userName:  '',
                  roleId: '',
                  loginName:  '',
                  userMobile:  '',
                  userMail: '',
                  orgNo: '',
                  orgName: '',
                  userId: 0,
                  dataOrgList:'',
                  dataOrgNameList: ''
              }
            }
        },
        component: {
        },
        methods: {
            getFrom: function (val) {
 
              if (val.combine) {
                if (this.searchParam[val.key.split('.')[1]] !== val.val) {
                  this.searchParam = this.cleanSearchData(this.searchParam);
                  this.getOption(val, true);
                  this.getParams(1);
                }
              }  else {
                if(val.key == 'formData.orgNo') {
                   this.formData.orgName = val.text;
                }
                 this.getOption(val, true);
              }
            },
            removeSearchData: function (index) {
                let key = this.deepSearchData[index].key;
                this.searchParam[key] = this.$refs['inputs'][0].search = '';
                this.getParams(1)
            },
            submitData: function () {
                let that = this;
                if(this.formData.dataOrgList.toString() == '') {
                    that.alerts.show({
                        msg: '管辖区域不能为空'
                    });
                  return 
                }
                this.formData.dataOrgList = this.formData.dataOrgList.toString().split(',');
                let arr, url;
                switch ( that.operate) {
                  case 0:
                      arr = ['userName', 'roleId', 'loginName', 'userMobile', 'userPassword', 'orgNo', 'dataOrgList'];
                      url = 'user/add';
                      break;
                  case 1:
                    arr =  ['userName', 'roleId', 'loginName', 'userMobile', 'orgNo', 'dataOrgList'];
                    url = 'user/edit';
                    this.formData.userId = this.formData.id;
                    break;
                }
               
                that.regularTest({
                  emptyKey: arr,
                  prefix: 'formData',
                  callback: function () {
                      that.getRequest({
                        url: url,
                        method: 'post',
                        param: that.formData,
                        success: function (data) {
                          that.cancelDialog();
                          that.alerts.show({
                            msg: data.message
                          });
                          that.refreshTable();
                        },
                        error: function (data) {
                          that.alerts.show({
                            msg: data.message
                          });
                        }
                      })
                  }
                })
            },
            //显示编辑和添加人员的弹出框
            operateUser: function (data) {
                let that =  this;
                if (data !== null) {
                    that.operate = 1;
                    that.formData = that.extendObj(that.formData, data)
                } else {
                  that.operate = 0;
                  that.formData = that.cleanSearchData(that.formData)
                }
                that.showDialog = true;
                setTimeout(function () {
                    that.$refs.addUser.show();
                });
            },
            removeUser: function () {
                let that = this;
                this.getConfirm({
                  emptyMsg: '请先选择要删除的人员!',
                  confirmMsg: '确定要删除选中的人员',
                  callback:  {
                    confirm: function () {
                      that.deleteUser();
                    }
                  }
                });
            },
            resetSearch: function () {
               let that = this;
               that.$refs['inputs'][0].search = '';
               that.searchParam = that.cleanSearchData(this.searchParam);
               that.getParams(1)
            },
            deleteUser: function () {
                let that = this;
                this.deleteData({
                    url: 'user/delete',
                    params: ['id'],
                    callback: function (data) {
                      that.resetSearch()
                    }
                })
            },
            refreshTable: function () {
                this.getParams(1)
            },
            getParams: function (pageNumber) {
              this.deepSearchData =  [];
              let arr = this.getSearchData(this.searchParam);
              let labelName = '';
              for(let key in arr) {
                let value = arr[key];
                switch (key) {
                  case 'userName':
                    labelName = '人员姓名';
                    break;
                  case 'userMobile':
                    labelName = '联系电话';
                    break;
                  case 'roleName':
                    labelName = '人员角色';
                    break;
                }
                this.deepSearchData.push({label: labelName, value: value, key: key, flag: true})
              }
              this.getHeight();
              this.searchTable(arr, pageNumber);
            },
            clickTableRow: function (val) {
                    this.operateUser(val.val);
            },
            cancelDialog: function () {
               this.formData = this.cleanSearchData(this.formData);
               this.showDialog = false;
               this.$refs.addUser.close();
            },
            windowChange() {
              let that = this;
              that.getHeight();
              /*window.onresize = function () {
                that.getHeight();
              }*/
            },
            init: function () {
              let that = this;
              /*window.onresize = function () {
                that.windowChange();
              }*/

            }
        },
        computed: {
            alerts: function () {
                return this.$refs.alerts
            },
            dialogFrom: function () {
                let data = this.formData;
                if(this.operate == 1 &&  data.dataOrgList instanceof Array) {
                   data.dataOrgList =  data.dataOrgList.join(',');
                
                }
                 if(this.operate == 1 &&  data.dataOrgNameList instanceof Array) {
                  data.dataOrgNameList =  data.dataOrgNameList.join(',') 
                }
                let width = 'calc(100% - 6em)';
                return [
                  {
                    classes: 'halfWidth',
                    type: 'select',
                    label: '部门机构',
                    isEmpty: false,
                    options: {
                      type: 'complexSelect',
                      key: 'formData.orgNo',
                      cs: 'server',
                      width:width,
                      url: 'org/queryOrgTreeInfo',
                      method: 'post',
                      text: 'orgName',
                      value: 'orgNo',
                      constant:  this.operate === 1 ? {orgName: data.orgName, orgNo: data.orgNo} : '',
                      rowName: 'data'
                    }
                  },
                  {
                    classes: 'halfWidth hasMarginLi',
                    type: 'input',
                    isEmpty: false,
                    label: '人员姓名',
                    options: {
                      width: width,
                      type: 'normalInput',
                      value: data.userName || '',
                      placeHolder: '可包含中英文、下划线',
                      key: 'formData.userName',
                      regular: 'name',
                      length: 20,
                      errorMsg: '人员姓名：不允许输入@、#等特殊字符'
                    }
                  },
                  {
                    classes: 'halfWidth', type: 'select', isEmpty: false, label: '人员角色',
                    options: {
                      key: 'formData.roleId',
                      cs: 'server',
                      url: 'role/queryAll',
                      method: 'post',
                      width: width,
                      constant:   this.operate === 1 ? {id: data.roleId} : '',
                      text: 'roleName',
                      value: 'id',
                      rowName: 'data'

                    }
                  },
                  {
                    classes: 'halfWidth hasMarginLi',
                    type: 'input',
                    isEmpty: true,
                    label: '人员邮箱',
                    options: {
                      type: 'normalInput',
                      value: data.userMail || '',
                      placeHolder: '如：11636567@qq.com',
                      length: 30,
                      width: width,
                      key: 'formData.userMail',
                      regular: 'email',
                      errorMsg: '邮箱格式：如1139725780@qq.com'
                    }
                  },
                  {
                    classes: 'halfWidth',
                    type: 'input',
                    isEmpty: false,
                    label: '联系电话',
                    options: {
                      width: width,
                      type: 'normalInput',
                      value: data.userMobile || '',
                      placeHolder: '11位数字',
                      length: 13,
                      key: 'formData.userMobile',
                      regular: 'phone',
                      errorMsg: '联系方式为11位手机号或者座机号',
                      limit: 'phone'
                    }
                  },
                  {
                    classes:'halfWidth hasMarginLi',
                    type: 'input',
                    isEmpty: false,
                    label: '登录账号',
                    options: {
                      width: width,
                      type: this.operate === 0 ? 'normalInput' : 'disabledInput',
                      value: data.loginName || '',
                      placeHolder: '不超过20个字符',
                      length: 20,
                      key: 'formData.loginName',
                      regular: 'name',
                      errorMsg: '登录账号：不允许输入@、#等特殊字符'
                    }
                  },
                  {
                    classes: 'halfWidth',
                    type: 'input',
                    isEmpty: false,
                    unShow: this.operate === 1,
                    label: '登录密码',
                    options: {
                      width: width,
                      type:  'normalInput',
                      value: data.userPassword,
                      placeHolder: '至少5位，不包含文字，可输入特殊字符@ # $ ! % ^ & * （英式键盘）',
                      length: 20,
                      key: 'formData.userPassword',
                      regular: 'password',
                      errorMsg: '登录密码：至少5位，不包含文字，可输入特殊字符@ # $ ! % ^ & * （英式键盘）'
                    }
                  },
                  {
                    classes: this.operate === 1 ? 'allWidth' : 'halfWidth hasMarginLi',
                    type: 'select',
                    label: '管辖区域',
                    isEmpty: false,
                    options: {
                      type: 'complexSelect',
                      MultiSelect: true,
                      key: 'formData.dataOrgList',
                      cs: 'server',
                      width:width,
                      url: 'org/queryOrgTreeInfo',
                      param: {orgNo: this.formData.orgNo},
                      method: 'post',
                      text: 'orgName',
                      value: 'orgNo',
                      constant:  this.operate === 1  ? {orgNo: data.dataOrgList, orgName: data.dataOrgNameList} : '',
                      rowName: 'data'
                    }
                  },
                  {
                    classes: 'allWidth',
                    type: 'input',
                    isEmpty: true,
                    label: '备注信息',
                    options: {
                      width: width,
                      type: 'textArea',
                      value: data.remark || '',
                      placeHolder: '不超过200个字符',
                      key: 'formData.remark',
                      regular: 'remark',
                      errorMsg: '备注：200个任意字符'
                    }
                  }
                ]
            }
        },
        mounted: function () {
            this.isIn();
            this.getAction();
        }

    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
</style>



